.sorting__item {
  margin: 10px; }
  .sorting__item a {
    color: #60452F;
    text-decoration: none;
    font-size: 20px; }
    .sorting__item a:hover {
      text-decoration: underline; }

.sorting {
  justify-content: space-around;
  margin-bottom: 30px; }

.items__wrapper {
  position: relative;
  width: 450px;
  display: flex; }

.items__table {
  width: 40%;
  margin-top: 25px; }
  .items__table tr {
    background-color: inherit; }

.items__wrapper-image {
  box-sizing: border-box;
  padding: 10px;
  width: 40%; }

.items__image {
  width: 100%; }

.items__button {
  border: none;
  background-color: inherit;
  position: absolute;
  right: 5px;
  top: 5px; }
  .items__button::before {
    content: "";
    position: absolute;
    top: 5px;
    right: 3px;
    width: 19px;
    height: 3px;
    background-color: #000000;
    transform: rotate(45deg);
    border-radius: 5px; }
  .items__button::after {
    content: "";
    position: absolute;
    top: 5px;
    right: 3px;
    width: 19px;
    height: 3px;
    background-color: #000000;
    transform: rotate(-45deg);
    border-radius: 5px; }

.items__input-checkbox {
  display: none; }
  .items__input-checkbox + label {
    position: relative;
    margin-left: 20px; }
    .items__input-checkbox + label::before {
      content: "";
      top: -4px;
      left: -25px;
      width: 25px;
      height: 25px;
      background: url("../img/checkbox-off.png") no-repeat;
      position: absolute; }
  .items__input-checkbox:checked + label::after {
    content: "";
    width: 25px;
    top: -4px;
    left: -25px;
    height: 25px;
    background: url("../img/checkbox-on.png") no-repeat;
    position: absolute; }

.items__input-number {
  width: 45px;
  text-align: center; }

@media (max-width: 1000px) {
  .items__contaiter {
    width: 768px; } }

@media (max-width: 768px) {
  .items__contaiter {
    width: 320px; }
  .items__inner {
    flex-direction: column;
    display: flex;
    justify-content: space-around; }
  .items__wrapper {
    width: 100%;
    justify-content: center;
    align-items: center; }
  .items__wrapper-image {
    width: 100%;
    padding-top: 30px; }
  .items__item {
    width: 100%;
    margin-bottom: 25px; }
  .items__table {
    width: 90%;
    margin: 0 auto; } }

.pagination {
  margin: 30px 0;
  display: flex;
  justify-content: center; }

.pagination__list {
  list-style: none;
  display: flex;
  padding: 0;
  justify-content: space-around;
  width: 200px; }

.pagination__link {
  transform: scale(1);
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  border: 1px solid #60452F;
  border-radius: 5px;
  padding: 10px;
  background: #CAAA8E; }
  .pagination__link--active {
    background: initial;
    color: #60452F; }
  .pagination__link:hover {
    background: red;
    transition: .3s;
    transform: scale(2.1); }

body {
  width: 100%;
  margin: 0;
  background-color: #F8ECE3;
  color: #60452F;
  font-family: sans-serif; }

.visually-hidden {
  display: none; }

.container {
  margin: 0 auto;
  position: relative;
  height: 50px;
  width: 1050px; }

@media (max-width: 1000px) {
  .container {
    width: 768px; } }

@media (max-width: 700px) {
  .container {
    width: 320px; } }
